import React, { Fragment, useState, useContext, useEffect } from "react";
import { NavLink } from "react-router-dom";
import Card from "../../UI/Card";
import AccountDetails from "./AccountDetails";
import NewPassword from "./NewPassword";
import AuthContext from "../../../Store/auth-context";
import classes from "./UserProfile.module.css";

const UserProfile = (props) => {
  const [changePassword, setChangePassword] = useState(false);
  const [myAccountDetails, setMyAccountDetails] = useState(true);
  const [accountDetails, setAccountDetails] = useState([]);
  const [isAdmin, setIsAdmin] = useState(false);

  const authCtx = useContext(AuthContext);

  const changePasswordHandler = () => {
    setChangePassword(true);
    setMyAccountDetails(false);
  };

  const accountDetailsHandler = () => {
    setMyAccountDetails(true);
    setChangePassword(false);
  };

  useEffect(() => {
    const fetchAccountDetails = async () => {
      const res = await fetch(
        `https://sl-rc-store-default-rtdb.asia-southeast1.firebasedatabase.app/userData/${authCtx.localId}.json`
      );
      const resData = await res.json();

      const loadedAccountDetails = [];

      for (const key in resData) {
        loadedAccountDetails.push({
          key: key,
          id: key,
          userName: resData[key].userName,
          email: resData[key].email,
          mobile: resData[key].mobileNum,
        });
      }
      if (authCtx.localId === "nHb0gAendUVUbs66zSYiS7CAirk1") {
        setIsAdmin(true);
        console.log(isAdmin);
      }

      setAccountDetails(loadedAccountDetails);
    };

    fetchAccountDetails();
  }, []);

  const logoutHandler = () => {
    authCtx.logout();
  };

  const accountDetailsList = accountDetails.map((details) => (
    <AccountDetails
      key={details.key}
      userName={details.userName}
      email={details.email}
      mobile={details.mobile}
    />
  ));

  return (
    <Fragment>
      <div className={classes.user_profile}>
        <Card>
          <div className={classes.menu}>
            {isAdmin && <NavLink to="/admin/addnewpart">Add New Part</NavLink>}
            <p>My Orders</p>
            <p onClick={accountDetailsHandler}>Account Details</p>
            <p>Addresses</p>
            <p onClick={changePasswordHandler}>Change Password</p>
            <button onClick={logoutHandler}>Logout</button>
          </div>
        </Card>
        <div className={classes.body}>
          {myAccountDetails && accountDetailsList}
          {changePassword && <NewPassword />}
        </div>
      </div>
    </Fragment>
  );
};

export default UserProfile;
